@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;

  .wrapper {
    display: flex;
    flex-direction: row;
    margin-top: 10px;

    .left-side {
      .title {
        font-weight: bold;
        margin-bottom: 10px;
      }

      width: 20%;
    }

    .right-side {
      flex: 1;

      .header {
        flex: 1;
      }

      .table-header {
        display: flex;
        flex-direction: row;
        .search {
          flex: 1;
        }
        .contract {
          margin-left: 5px;
          display: flex;
          flex-direction: column;
          text-align: center;
        }
      }

      .table {
        flex: 1;

        .hidden {
          display: none;
        }

        th {
          padding: 5px;
          font-size: small;

          div {
            div {
              margin-bottom: unset !important;
            }
          }

          .search {
            width: 100%;
            font-size: unset;
            font-weight: normal;
            padding: 5px;
          }
        }

        td {
          vertical-align: unset;
          font-size: small;
          padding: 5px;
          white-space: break-spaces;
        }

        .cell {
          display: flex;
          flex-direction: column;
        }
      }

      .footer {
        flex: 1;
      }
    }
  }
}

.center {
  text-align: center;
  font-style: italic;
}
