@import 'styles/color.scss';

.root {
  flex: 1;
  display: flex;
  flex-direction: row;

  .sidebar {
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    button {
      margin-bottom: 10px;
    }
  }
  .content {
    flex: 1;
    padding-left: 10px;
    display: flex;
    flex-direction: column;

    .title {
      margin-bottom: 5px;
    }

    .wrapper {
      flex: 1;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
    }
  }
}
